import { useRef, useState } from 'react';
import { helperFunction } from '../helper';
import { supabase } from '../lib/api';
import { ReactComponent as Github } from '../assets/icons2/w/mdi_github.svg';
import { ReactComponent as Google } from '../assets/icons2/w/mdi_google.svg';
import { ReactComponent as Facebook } from '../assets/icons2/w/mdi_facebook.svg';
import { ReactComponent as Login } from '../assets/icons2/w/mdi_login.svg';
import { ReactComponent as Register } from '../assets/icons2/extra/mdi_account-plus-outline.svg';
import Logo from './Logo';
import Message from './Message';

const Auth = () => {
  const [helperText, setHelperText] = useState({
    error: null,
    text: null,
    type: null,
  });
  const emailRef = useRef();
  const passwordRef = useRef();

  const handleLogin = async (type) => {
    const email = emailRef.current?.value;
    const password = passwordRef.current?.value;

    // eslint-disable-next-line
    const { user, error } =
      type === 'LOGIN'
        ? await supabase.auth.signIn({ email, password })
        : await supabase.auth.signUp({ email, password });

    if (error) {
      helperFunction(true, error.message, 'alert-error', setHelperText, 3000);
    } else {
      if (type === 'REGISTER') {
        const { data: profileData, error: profileError } = await supabase
          .from('profile')
          .insert({ user_id: user.id, name: 'User' });
        if (profileError) console.error(profileError);
        else console.log(profileData);
      }

      helperFunction(
        false,
        'An email has been sent to you for verification!',
        'alert-info',
        setHelperText,
        9000
      );
    }
  };

  const handleOAuthLogin = async (provider) => {
    // You need to enable the third party auth you want in Authentication > Settings
    // Read more on: https://supabase.io/docs/guides/auth#third-party-logins
    let { error } = await supabase.auth.signIn({ provider });
    if (error) console.error('Error: ', error.message);
  };

  // eslint-disable-next-line
  const forgotPassword = async (e) => {
    // Read more on https://supabase.io/docs/reference/javascript/reset-password-email#notes
    e.preventDefault();
    const email = prompt('Please enter your email:');

    if (email === null || email === '') {
      helperFunction(
        true,
        'You must enter your email.',
        'alert-info',
        setHelperText,
        9000
      );
    } else {
      let { error } = await supabase.auth.api.resetPasswordForEmail(email);
      if (error) {
        console.error('Error: ', error.message);
      } else {
        helperFunction(
          false,
          'Password recovery email has been sent.',
          'alert-info',
          setHelperText,
          9000
        );
      }
    }
  };

  return (
    <div className='container'>
      <div className='row justify-content-md-center my-3'>
        <Logo />
        <div className='col-md-6 col-12 my-5'>
          <h2>Login</h2>
          <div className='mb-4 field'>
            <label htmlFor={'email'}>
              <span>*</span>Email:
            </label>
            <input type={'email'} name={'email'} ref={emailRef} required />
          </div>
          {/*  */}
          <div className='mb-4 field'>
            <label htmlFor={'password'}>
              <span>*</span>
              Password:
            </label>
            <input
              type={'password'}
              name={'password'}
              ref={passwordRef}
              required
            />
          </div>
          {/*  */}
          <div className='flex gap-0'>
            <button
              className='btn-inline btn-secondary flex-1'
              type='submit'
              onClick={() => handleLogin('REGISTER').catch(console.error)}
            >
              <Register/> Sign Up
            </button>

            <button
              className='btn-inline btn-primary flex-1'
              onClick={() => handleLogin('LOGIN')}
              type='button'
            >
              <Login/> Sign In
            </button>
          </div>
        </div>

        {/* <span onClick={forgotPassword}>Forgot Password?</span> */}
        {!!helperText.text && (
          <Message data={helperText.text} type={helperText.type} />
        )}

        <div>
          <div>
            <div>
              <div />
            </div>
            <div>
              <small>Or continue with</small>
            </div>
          </div>

          <div>
            <div className='flex wrap'>
              <button
                onClick={() => handleOAuthLogin('github')}
                type='button'
                className='btn-icon btn-primary me-1'
              >
               <Github/>
              </button>

              <button
                onClick={() => handleOAuthLogin('google')}
                type='button'
                className='btn-icon btn-primary me-1'
              >
               <Google/>
              </button>

              <button
                onClick={() => handleOAuthLogin('facebook')}
                type='button'
                className='btn-icon btn-primary me-1'
              >
               <Facebook/>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Auth;
