import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router';
import { Link } from 'react-router-dom';
import { ReactComponent as Back } from '../assets/icons2/mdi_arrow-left.svg';
import { getMovies, getTags } from '../store/movies';
import Card from './Card';
import Loading from './Loading';

const Genre = () => {
  const { term } = useParams();
  const history = useHistory();
  const dispatch = useDispatch();

  const { data, loading } = useSelector((state) => state.movies.movies);
  const { data: tags, loading: loadingTags } = useSelector(
    (state) => state.movies.tags
  );
  const back = () => history.goBack();

  useEffect(() => {
    dispatch(getMovies());

    dispatch(getTags());
  }, [term, dispatch]);

  return (
    <div>
      <div>
        <button className='btn-inline' onClick={back}>
          <Back /> Back
        </button>
      </div>

      <div>
        <ul className='flex gap-1 wrap flex-justify-center mt-4'>
          {loadingTags ? (
            <Loading />
          ) : (
            tags?.map((i) => (
              <li key={i}>
                <Link
                  className='btn-inline btn-secondary px-3'
                  to={`../genre/${i}`}
                >
                  {i}
                </Link>
              </li>
            ))
          )}
        </ul>
      </div>

      <h2>Movies</h2>

      <div className='flex wrap gap-1 flex-justify-center'>
        {!loading ? (
          data &&
          data.map((i) => (
            <Link to={`../movies/${i.id}`} key={i.id} className='flex'>
              <Card
                name={i.name}
                storyline={i.storyline}
                date={i.year}
                poster={i.poster}
              />
            </Link>
          ))
        ) : (
          <Loading />
        )}
      </div>
    </div>
  );
};

export default Genre;
